import { NavigationInterface } from "./Navigation"

class NavigatorMock implements NavigationInterface {
    warning = () => {
        // tslint:disable-next-line:no-console
        console.warn("The Navigator API is only available inside of Framer X. Get it at http://framer.com/download")
    }

    goBack = () => this.warning()
    instant = () => this.warning()
    fade = () => this.warning()
    push = () => this.warning()
    modal = () => this.warning()
    overlay = () => this.warning()
    flip = () => this.warning()
    customTransition = () => this.warning()
}

/**
 * @internal
 */
export const navigatorMock = new NavigatorMock()
