import { ActionHandler } from "../render/types/Action"
import { addActionControls } from "../utils/addActionControls"
import { ControlType } from "../render/types/PropertyControls"
import { useNavigation } from "../components/useNavigation"

type TransitionType = "push" | "instant" | "fade" | "modal" | "overlay" | "flip"
const transitions: TransitionType[] = ["push", "instant", "fade", "modal", "overlay", "flip"]
const transitionTitles: string[] = ["Push", "Instant", "Fade", "Modal", "Overlay", "Flip"]

/**
 * @internal
 */
export interface NavigateToOptions {
    type: "goBack" | "showNext"
    // target: () => React.ReactNode | null
    transition: TransitionType
    appearsFrom: "left" | "right" | "top" | "bottom"
    transitionBackdropColor: string
}

/**
 * @internal
 */
export function useNavigateTo(options: NavigateToOptions): ActionHandler {
    const navigation = useNavigation()
    return () => {
        if (options.type === "goBack") {
            navigation.goBack()
            return
        }
        switch (options.transition) {
            default: // TODO: implement
        }
    }
}

addActionControls(useNavigateTo, "Navigate To", {
    type: {
        type: ControlType.SegmentedEnum,
        options: ["goBack", "showNext"],
        optionTitles: ["Go back", "Show next"],
        defaultValue: "goBack",
        title: "Type",
    },
    transition: {
        type: ControlType.Enum,
        options: transitions,
        optionTitles: transitionTitles,
        defaultValue: "push",
        title: "Transition",
        hidden: info => info.type === "goBack",
    },
    appearsFrom: {
        type: ControlType.SegmentedEnum,
        options: ["top", "right", "bottom", "left"],
        title: "Appear from",
        defaultValue: "right",
        hidden: info => {
            if (info.type === "goBack") return true
            switch (info.transition) {
                case "flip":
                case "overlay":
                case "push":
                    return false
                default:
                    return true
            }
        },
    },
    transitionBackdropColor: {
        type: ControlType.Color,
        title: "Backdrop",
        defaultValue: "rgba(4,4,15,.4)",
        hidden: info => {
            if (info.type === "goBack") return true
            switch (info.transition) {
                case "overlay":
                case "modal":
                    return false
                default:
                    return true
            }
        },
    },
})
