import { ActionHandler } from "../render/types/Action"
import { addActionControls } from "../utils/addActionControls"
import { ControlType } from "../render/types/PropertyControls"

/**
 * @internal
 */
export interface LogOptions {
    type: "event" | "message" | "count"
    message: string
}

/**
 * @internal
 */
export function useLog(options: LogOptions): ActionHandler {
    return (event: any) => {
        switch (options.type) {
            case "event":
                // Because our custom console is async, we need to persist the event. Otherwise React will re-use
                // the event object between events. See https://reactjs.org/docs/events.html#event-pooling for details.
                if (typeof event["persist"] === "function") {
                    event.persist()
                }
                // tslint:disable-next-line:no-console
                console.log(event)
                break
            case "message":
                // tslint:disable-next-line:no-console
                console.log(options.message)
                break
            case "count":
                // tslint:disable-next-line:no-console
                console.count(options.message)
                break
        }
    }
}

addActionControls(useLog, "Log", {
    type: {
        type: ControlType.Enum,
        options: ["event", "message", "count"],
        optionTitles: ["Event", "Message", "Count"],
        defaultValue: "event",
        title: "Type",
    },
    message: {
        type: ControlType.String,
        title: "Message",
        hidden(props) {
            return props.type === "event"
        },
    },
})
