import { ActionHandler } from "../render/types/Action"
import { addActionControls } from "../utils/addActionControls"
import { ControlType } from "../render/types/PropertyControls"

/**
 * @internal
 */
export interface OpenURLOptions {
    url: string
}

/**
 * @internal
 */
export function useOpenURL(options: OpenURLOptions): ActionHandler {
    return () => {
        const url = options.url.includes(":") ? options.url : `http://${options.url}`
        window.open(url)
    }
}

addActionControls(useOpenURL, "Open URL", {
    url: { type: ControlType.String, placeholder: "www.framer.com", title: "URL" },
})
